<template>
    <div>
        <el-table :data="dataList" stripe style="width: 100%" row-key="id">
            <el-table-column prop="id" label="ID" width="50">
                <template v-slot="scope">
                    {{ scope.$index + 1 }}
                </template>
            </el-table-column>
            <el-table-column prop="table" label="数据表"></el-table-column>
            <el-table-column prop="model" label="AR模型"></el-table-column>
            <el-table-column prop="desc" label="说明"></el-table-column>
            <el-table-column prop="count" label="数据条数" width="100"></el-table-column>
            <el-table-column label="操作" width="100">
                <template v-slot="scope">
                    <el-col>
                        <el-button type="danger" class="c-red" size="mini" @click="clear(scope.$index)" icon="el-icon-delete">清除</el-button>
                    </el-col>
                </template>
            </el-table-column>
        </el-table>
    </div>
</template>

<script>
export default {
    name: "MaintainableTables",
    components: {},
    mounted() {
        this.load()
    },
    data() {
        return {
            isLoading: false,
            dataList: [],
        }
    },
    methods: {
        clear(index) {
            let item = this.dataList[index]
            this.$message.confirm({message: '确定要清空此表数据吗?'}, () => {
                this.$loading.show('删除中')
                this.$request.post('/maintainable/clear', {model: item.model}).then(result => {

                    if (result.errCode) {
                        this.$message.alert(result.message)
                    } else {
                        this.$message.success(`成功删除${result.data}条数据`)
                        this.load()
                    }
                }).catch(() => {
                    this.$message.error('请求错误')
                }).finally(() => {
                    this.$loading.close()
                })
            })
        },
        //获取列表
        load() {
            this.isLoading = true;
            this.$request.get('/maintainable/tables').then(result => {
                if (result.errCode) {
                    this.$message.error(result.message);
                } else {
                    this.dataList = result.data;
                }
            }).catch(error => {
                this.$message.error(error);

            }).then(() => {
                this.isLoading = false;
            });
        }
    }
}
</script>
